"use client";

import React, { useState } from 'react';
import { useGrowwStrength } from '@/hooks/useStocks';
import { ArrowUp, ArrowDown, Search, BarChart2 } from 'lucide-react';

export default function GrowwFullDataPage() {
    const { data: stocks, isLoading } = useGrowwStrength({});
    const [search, setSearch] = useState("");

    const filtered = stocks?.filter(s =>
        s.symbol.toLowerCase().includes(search.toLowerCase()) ||
        s.name.toLowerCase().includes(search.toLowerCase())
    ) || [];

    // Gainers / Losers
    const gainers = [...(stocks || [])].sort((a, b) => b.current_change - a.current_change).slice(0, 5);
    const losers = [...(stocks || [])].sort((a, b) => a.current_change - b.current_change).slice(0, 5);

    return (
        <div className="container mx-auto p-4 space-y-6">
            <header className="flex justify-between items-center">
                <div>
                    <h1 className="text-3xl font-bold bg-gradient-to-r from-green-600 to-green-400 bg-clip-text text-transparent">
                        Market Overview
                    </h1>
                    <p className="text-muted-foreground">Real-Time Data from Groww API</p>
                </div>
                <div className="relative">
                    <Search className="absolute left-3 top-3 h-4 w-4 text-muted-foreground" />
                    <input
                        type="text"
                        placeholder="Search stocks..."
                        value={search}
                        onChange={(e) => setSearch(e.target.value)}
                        className="pl-9 pr-4 py-2 bg-card border rounded-full focus:outline-none focus:ring-2 focus:ring-green-500 w-64"
                    />
                </div>
            </header>

            {/* Market Stats Grid */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {/* Gainers */}
                <div className="bg-card/50 rounded-xl p-4 border border-green-500/20">
                    <h2 className="text-xl font-semibold mb-4 flex items-center text-green-500">
                        <ArrowUp className="mr-2" /> Top Gainers
                    </h2>
                    <div className="space-y-3">
                        {gainers.map(stock => (
                            <div key={stock.symbol} className="flex justify-between items-center p-2 hover:bg-muted/50 rounded-lg cursor-pointer">
                                <div>
                                    <div className="font-bold">{stock.symbol}</div>
                                    <div className="text-xs text-muted-foreground">{stock.name}</div>
                                </div>
                                <div className="text-right">
                                    <div className="text-green-500 font-mono">₹{stock.current_price}</div>
                                    <div className="text-xs text-green-400">+{stock.current_change}%</div>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>

                {/* Losers */}
                <div className="bg-card/50 rounded-xl p-4 border border-red-500/20">
                    <h2 className="text-xl font-semibold mb-4 flex items-center text-red-500">
                        <ArrowDown className="mr-2" /> Top Losers
                    </h2>
                    <div className="space-y-3">
                        {losers.map(stock => (
                            <div key={stock.symbol} className="flex justify-between items-center p-2 hover:bg-muted/50 rounded-lg cursor-pointer">
                                <div>
                                    <div className="font-bold">{stock.symbol}</div>
                                    <div className="text-xs text-muted-foreground">{stock.name}</div>
                                </div>
                                <div className="text-right">
                                    <div className="text-red-500 font-mono">₹{stock.current_price}</div>
                                    <div className="text-xs text-red-400">{stock.current_change}%</div>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>

            {/* Full List */}
            <div className="bg-card rounded-xl p-6 border shadow-sm">
                <h2 className="text-xl font-semibold mb-4 flex items-center">
                    <BarChart2 className="mr-2" /> Full Market Data ({filtered.length})
                </h2>
                <div className="overflow-x-auto">
                    <table className="w-full text-sm">
                        <thead className="bg-muted/50 text-left">
                            <tr>
                                <th className="p-3 rounded-l-lg">Symbol</th>
                                <th className="p-3">Price</th>
                                <th className="p-3">Change %</th>
                                <th className="p-3">High/Low</th>
                                <th className="p-3 rounded-r-lg">Volume</th>
                            </tr>
                        </thead>
                        <tbody>
                            {filtered.slice(0, 20).map(stock => (
                                <tr key={stock.symbol} className="border-b border-border/50 hover:bg-muted/30">
                                    <td className="p-3 font-medium">{stock.symbol}</td>
                                    <td className="p-3 font-mono">₹{stock.current_price}</td>
                                    <td className={`p-3 font-mono ${stock.current_change >= 0 ? 'text-green-500' : 'text-red-500'}`}>
                                        {stock.current_change > 0 ? '+' : ''}{stock.current_change}%
                                    </td>
                                    <td className="p-3 text-muted-foreground">
                                        {stock.day_high} / {stock.day_low}
                                    </td>
                                    <td className="p-3 font-mono text-muted-foreground">
                                        {stock.volume.toLocaleString()}
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    );
}
